





















import Vue from 'vue'
export default Vue.extend({
  props: {
    name: {
      type: String,
      required: true,
    },
    age: {
      type: Number,
      required: true,
    },
    careGiver: {
      type: Object,
      required: true
    },
    events: Array
  },
  methods: {

    careGiverToString(data: {firstName: string, lastName: String}) {
      return `${data.firstName} ${data.lastName}`;
    }
  }
})
